@use "../../Assets/Styles/variables" as *;

.input-wrapper {
  border-radius: 5px;
  background-color: white;
  display: flex;
  align-items: center;
  box-shadow: 0px 0px 20px 1px $l_darkGrayBlue;

  &.dark {
    background-color: $d_vDarkDesatBlue;
    box-shadow: 0px 0px 20px 1px $d_vDarkDesatBlue;
  }

  .input-circle {
    width: 20px;
    height: 20px;
    min-width: 20px;
    border-radius: 50%;
    border: 1px solid $l_vLightGrayBlue;
    margin: 20px;
    &.dark {
      border: 1px solid $d_darkGrayishBlue;
    }
  }

  .input-field-wrapper {
    width: 85%;
    .input-field {
      font-size: 18px;
      line-height: 30px;
      border: none;
      width: 100%;
      margin-top: 5px;
      background-color: inherit;

      &.dark {
        color: $d_lightGrayBlueH;
      }

      &:focus {
        outline: none;
      }
    }
  }

  .btn {
    display: none;
  }
}
